/* Stiluri generale pentru Home */
.home-container {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 0;
    padding: 0;
    height: 100vh;
    position: relative;
    
    font-family: Arial, sans-serif;
  }
  
  .welcome-text {
    font-size: 100px;
    font-weight: 700;
    color: #f9f9f9;
    margin-left: 500px;
    margin-top: 400px;
    text-align: center;
  }
  
  .role-text {
    margin-top: 100px;
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 20px;
    color: #f9f9f9;
    font-weight: 600;
  }
  