.navbar {
    display: flex;
    justify-content: flex-end; /* Alinierea elementelor la dreapta */
    align-items: center;
    background-color: white; /* Bara de navigare albă */
    padding: 20px 50px; /* Mai mult spațiu pentru lățimea barei */
    position: fixed; /* Bara va rămâne la începutul paginii */
    top: 0;
    left: 0;
    width: 100%; /* Se întinde pe toată lățimea ecranului */
    z-index: 1000; /* Asigură-te că bara de navigare este deasupra altor elemente */
    font-size: 24px; /* Dimensiunea fontului pentru a fi mai mare */
    border-top: 3px solid #4c00b4; /* Border mov sus */
    border-left: 3px solid #4c00b4; /* Border mov stânga */
    border-right: 3px solid #4c00b4;
}

.navbar ul {
    list-style: none;
    display: flex;
    gap: 50px; /* Mai mult spațiu între link-uri */
    font-size: 24px; /* Mărirea dimensiunii fontului pentru lista de elemente */
}

.navbar li {
    color: #4c00b4; /* Culoarea textului link-urilor */
}

.navbar a {
    color: #4c00b4; /* Textul link-urilor va fi mov */
    text-decoration: none;
    font-weight: bold; /* Text îngroșat */
    font-size: 24px; /* Mărimea fontului pentru link-uri */
    transition: color 0.3s; /* Tranziție pentru schimbarea culorii */
}

.navbar a:hover {
    color: #3c009d; /* Culoare la hover, un mov mai închis */
}
